import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class footer extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    return (
      <div>
        {/*Call To Action 2 Content Start*/}
        <section className="tj-cal-to-action2">
          <div className="container">
            <div className="row">
              <div className="col-md-9 col-sm-9">
                <div className="cta-tagline">
                  <h2>Register as a Driver or Owner</h2>
                </div>
              </div>
              <div className="col-md-3 col-sm-3">
                <div className="cta-btn">
                  <Link to="/register">
                    Register Now{" "}
                    <i
                      className="fa fa-arrow-circle-right"
                      aria-hidden="true"
                    />
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/*Footer Copyright Start*/}
        <section className="tj-copyright">
          <div className="container">
            <div className="row">
              <div className="col-md-6 col-sm-6">
                <p>
                  © Copyrights 2020 <Link to="/">Cab Booking</Link>. All Rights
                  Reserved.
                </p>
              </div>
              <div className="col-md-6 col-sm-6">
                <ul className="payment-icons">
                  <li>
                    <i className="fab fa-facebook" />
                  </li>
                  <li>
                    <i className="fab fa-twitter" />
                  </li>
                  <li>
                    <i className="fab fa-linkedin-in" />
                  </li>
                  <li>
                    <i className="fab fa-instagram" />
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
