import React, { Component } from "react";

export default class index extends Component {
  render() {
    return (
      <div>
        {/*Inner Banner Section Start*/}
        <div className="tj-inner-banner">
          <div className="container">
            <h2>Contact us</h2>
          </div>
        </div>
        {/*Inner Banner Section End*/}
        {/*Offer Content Start*/}
        <section className="tj-offers">
          <div className="row">
            {/*Offer Box Content Start*/}
            <div className="col-md-4 col-sm-6">
              <div className="offer-box">
                <img src="/assets/images/offer-icon1.png" alt="" />
                <div className="offer-info">
                  <h4>Best Price Guaranteed</h4>
                  <p>Lorem Ipsum dolor sit amet passages</p>
                </div>
              </div>
            </div>
            {/*Offer Box Content End*/}
            {/*Offer Box Content Start*/}
            <div className="col-md-4 col-sm-6">
              <div className="offer-box">
                <img src="/assets/images/offer-icon2.png" alt="" />
                <div className="offer-info">
                  <h4>24/7 Customer Care</h4>
                  <p>Lorem Ipsum dolor sit amet passages</p>
                </div>
              </div>
            </div>
            {/*Offer Box Content End*/}
            {/*Offer Box Content Start*/}
            <div className="col-md-4 col-sm-6">
              <div className="offer-box">
                <img src="/assets/images/offer-icon3.png" alt="" />
                <div className="offer-info">
                  <h4>Home Pickups</h4>
                  <p>Lorem Ipsum dolor sit amet passages</p>
                </div>
              </div>
            </div>
            {/*Offer Box Content End*/}
          </div>
        </section>
        {/*Offer Content End*/}
        <div className="tj-map">
          <div className="mapInner">
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d30703620.672472216!2d64.4139098985941!3d20.050418843029934!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x30635ff06b92b791%3A0xd78c4fa1854213a6!2sIndia!5e0!3m2!1sen!2sin!4v1607774057429!5m2!1sen!2sin"
              width="100%"
              height={450}
              frameBorder={0}
              style={{ border: 0 }}
              allowFullScreen
              aria-hidden="false"
              tabIndex={0}
            />
          </div>
        </div>
        {/*Contact Section Start*/}
        <section className="tj-contact-section">
          <div className="container">
            <div className="row">
              <div className="col-md-12 col-sm-12">
                <div className="tj-heading-style">
                  <h3>Contact Us</h3>
                  <p>
                    Lorem Ipsum passages, and more recently with desktop
                    publishing software like aldus pageMaker including versions
                    of all the Lorem Ipsum generators
                  </p>
                </div>
              </div>
              <div className="col-md-8 col-sm-8">
                <div className="form-holder">
                  <form
                    method="POST"
                    className="tj-contact-form"
                    id="contact-form"
                  >
                    <div className="row">
                      <div className="col-md-6 col-sm-6">
                        {/*Inner Holder Start*/}
                        <div className="inner-holder">
                          <label htmlFor="name">Name</label>
                          <input
                            placeholder="Enter Your Name"
                            name="name"
                            type="text"
                            id="name"
                            required
                          />
                        </div>
                        {/*Inner Holder End*/}
                      </div>
                      <div className="col-md-6 col-sm-6 no-pad">
                        {/*Inner Holder Start*/}
                        <div className="inner-holder">
                          <label htmlFor="email">Email</label>
                          <input
                            placeholder="Enter Your Email"
                            name="email"
                            type="email"
                            id="email"
                          />
                        </div>
                        {/*Inner Holder End*/}
                      </div>
                      <div className="col-md-12 col-sm-12">
                        {/*Inner Holder Start*/}
                        <div className="inner-holder">
                          <label htmlFor="subject">Subject</label>
                          <input
                            placeholder="Your Subject"
                            name="subject"
                            type="text"
                            id="subject"
                          />
                        </div>
                        {/*Inner Holder End*/}
                      </div>
                      <div className="col-md-12 col-sm-12">
                        {/*Inner Holder Start*/}
                        <div className="inner-holder">
                          <label htmlFor="message">Message</label>
                          <textarea
                            name="message"
                            placeholder="Your Message"
                            id="message"
                            defaultValue={""}
                          />
                        </div>
                        {/*Inner Holder End*/}
                      </div>
                      <div className="col-md-12 col-sm-12">
                        <div className="inner-holder">
                          <button
                            className="btn-submit"
                            type="button"
                            id="submitBtn"
                          >
                            Send Message{" "}
                            <i
                              className="fa fa-arrow-circle-right"
                              aria-hidden="true"
                            />
                          </button>
                        </div>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
              <div className="col-md-4 col-sm-4">
                <div className="address-box">
                  <div className="add-info">
                    <span className="icon-map icomoon" />
                    <p>
                      Address placeholder
                      <br /> Address placeholder 2
                    </p>
                  </div>
                  <div className="add-info">
                    <span className="icon-phone icomoon" />
                    <p>
                      <a href="tel:1-234-567-7890">+1 234 567 7890</a>
                      <a href="tel:1-234-567-7892">+1 234 567 7891</a>
                    </p>
                  </div>
                  <div className="add-info">
                    <span className="icon-mail-envelope-open icomoon" />
                    <p>
                      <a href="mailto:support@example.com">
                        support@example.com
                      </a>
                      <a href="mailto:info@example.com">info@example.com</a>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/*Contact Section End*/}
      </div>
    );
  }
}
