import React, { Component } from "react";
import { Link } from "react-router-dom";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../Server/Utils/HttpClient";
import ReactJsAlert from "reactjs-alert"

export default class register extends Component {
  constructor(props) {
    super(props);

    this.state = {
      driverDetails: {
        fullName: '',
        email: '',
        password: '',
        phone: '',
        terms : false
      },
      ownerDetails: {
        fullName: '',
        email: '',
        password: '',
        phone: '',
        carModel: '',
        carNo: '',
        terms : false
      },
      alert : {
        type : '',
        title : '',
        decs : '',
        status : false
      }
    };
  }

  driverRegister = async () => {
    if (this.state.driverDetails.terms) {
      if(this.state.driverDetails.fullName != '' && this.state.driverDetails.email != '' && 
        this.state.driverDetails.phone != '' && this.state.driverDetails.password != ''){
        let data = {
          "name": this.state.driverDetails.fullName,
          "email": this.state.driverDetails.email,
          "mobile": this.state.driverDetails.phone,
          "password": this.state.driverDetails.password
        }
  
        let result = await HttpClient.requestData("driver/register", "POST", data);
        
        if (result && result.status) {
          this.setState({
            alert : {
              type : 'success',
              title : 'Success',
              decs : 'You have registered successfully !!!',
              status : true
            }
          })
        }else{
          this.setState({
            alert : {
              type : 'error',
              title : 'Error',
              decs : result.error[0].msg,
              status : true
            }
          })
        }
      }else{
        this.setState({
          alert : {
            type : 'warning',
            title : 'Warning',
            decs : 'Please fill all fields...',
            status : true
          }
        })
      }
      
    }else{
      this.setState({
        alert : {
          type : 'warning',
          title : 'Warning',
          decs : 'Please accept the terms & conditions...',
          status : true
        }
      })
    }
  }

  ownerRegister = async () => {
    if(this.state.ownerDetails.terms){
      if(this.state.ownerDetails.fullName != '' && this.state.ownerDetails.email != '' && 
        this.state.ownerDetails.phone != '' && this.state.ownerDetails.password != ''){
        let data = {
          "name": this.state.ownerDetails.fullName,
          "email": this.state.ownerDetails.email,
          "mobile": this.state.ownerDetails.phone,
          "password": this.state.ownerDetails.password
        }
    
        let result = await HttpClient.requestData("owner/register", "POST", data);
        console.log("result", result)
        if (result && result.status) {
          this.setState({
            alert : {
              type : 'success',
              title : 'Success',
              decs : 'You have registered successfully !!!',
              status : true
            }
          })
        }else{
          this.setState({
            alert : {
              type : 'error',
              title : 'Error',
              decs : result.error[0].msg,
              status : true
            }
          })
        }
      }else{
        this.setState({
          alert : {
            type : 'warning',
            title : 'Warning',
            decs : 'Please fill all fields...',
            status : true
          }
        })
      }
      
    }else{
      this.setState({
        alert : {
          type : 'warning',
          title : 'Warning',
          decs : 'Please accept the terms & conditions...',
          status : true
        }
      })
    }
  }

  render() {
    return (
      <div>
        <ReactJsAlert
          type= {this.state.alert.type}   // success, warning, error, info
          title= {this.state.alert.title}   // title you want to display
          status={this.state.alert.status}   // true or false
          quote= {this.state.alert.decs}
          Close={() => {
            this.state.alert.status = false;
            this.setState({})
          }}   // callback method for hide
        />
        <section className="tj-register d-flex">
          <div className="container m-auto">
            <div className="row">
              <div className="col-md-12 col-sm-12">
                <div className="tj_reg_inner">
                  <div className="tj-tabs">
                    <ul className="nav nav-tabs" role="tablist">
                      <li className="active">
                        <a href="#driverRegister" data-toggle="tab">
                          Register Driver
                        </a>
                      </li>
                      <li className="">
                        <a href="#ownerRegister" data-toggle="tab">
                          Owner Registration
                        </a>
                      </li>
                    </ul>
                  </div>
                  <div className="tab-content">
                    <div className="tab-pane active" id="driverRegister">
                      <form className="login-frm" method="POST">
                        <div className="row">
                          <div className="col-md-6">
                            <div className="field-holder">
                              <span className="far fa-user" />
                              <input
                                className="input_field"
                                type="text"
                                name="login_name"
                                placeholder="Enter your full Name"
                                value={this.state.driverDetails.fullName}
                                onChange={(val) => {
                                  this.state.driverDetails.fullName = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="field-holder">
                              <span className="far fa-envelope" />
                              <input
                                className="input_field"
                                type="email"
                                name="login_email"
                                placeholder="Enter your Email Address"
                                value={this.state.driverDetails.email}
                                onChange={(val) => {
                                  this.state.driverDetails.email = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="field-holder">
                              <span className="fas fa-lock" />
                              <input
                                className="input_field"
                                type="password"
                                name="password"
                                placeholder="Password"
                                value={this.state.driverDetails.password}
                                onChange={(val) => {
                                  this.state.driverDetails.password = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="field-holder">
                              <span className="fas fa-phone" />
                              <input
                                className="input_field"
                                type="number"
                                name="phone"
                                placeholder="Phone Number"
                                value={this.state.driverDetails.phone}
                                onChange={(val) => {
                                  this.state.driverDetails.phone = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div>
                          <div className="col-md-6">
                            <a href="#" className="forget-pass">
                              Forget Password?
                            </a>
                            <label
                              htmlFor="termsDriver"
                              className="mt-3 text-dark font-weight-normal"
                            >
                              <input
                                type="checkbox"
                                name="termsDriver"
                                id="termsDriver"
                                className="mr-3"
                                checked = {this.state.driverDetails.terms}
                                onClick = {() => {
                                  this.state.driverDetails.terms = !this.state.driverDetails.terms
                                  this.setState({})
                                }}
                              />
                              I accept all the stated terms &amp; conditions
                            </label>
                          </div>
                          <div className="col-md-6">
                            <button
                              type="button"
                              className="reg-btn"
                              onClick={this.driverRegister}
                            >
                              Signup{" "}
                              <i
                                className="fa fa-arrow-circle-right"
                                aria-hidden="true"
                              />
                            </button>
                            <button type="button" className="google-btn">
                              Login with Google{" "}
                              <i className="fab fa-google" aria-hidden="true" />
                            </button>
                          </div>
                        </div>
                      </form>
                    </div>
                    <div className="tab-pane" id="ownerRegister">
                      <form className="login-frm" method="POST">
                        <div className="row">
                          <div className="col-md-6">
                            <div className="field-holder">
                              <span className="far fa-user" />
                              <input
                                className="input_field"
                                type="text"
                                name="login_name"
                                placeholder="Enter Owner Name"
                                value={this.state.ownerDetails.fullName}
                                onChange={(val) => {
                                  this.state.ownerDetails.fullName = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="field-holder">
                              <span className="far fa-envelope" />
                              <input
                                className="input_field"
                                type="email"
                                name="login_email"
                                placeholder="Enter your Email Address"
                                value={this.state.ownerDetails.email}
                                onChange={(val) => {
                                  this.state.ownerDetails.email = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="field-holder">
                              <span className="fas fa-lock" />
                              <input
                                className="input_field"
                                type="password"
                                name="password"
                                placeholder="Password"
                                value={this.state.ownerDetails.password}
                                onChange={(val) => {
                                  this.state.ownerDetails.password = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div>
                          <div className="col-md-6">
                            <div className="field-holder">
                              <span className="fas fa-phone" />
                              <input
                                className="input_field"
                                type="number"
                                name="phone"
                                placeholder="Phone Number"
                                value={this.state.ownerDetails.phone}
                                onChange={(val) => {
                                  this.state.ownerDetails.phone = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div>
                          {/* <div className="col-md-6">
                            <div className="field-holder">
                              <span className="fa fa-car" />
                              <input
                                className="input_field"
                                type="text"
                                name="carModel"
                                placeholder="Enter Car Model"
                                value={this.state.ownerDetails.carModel}
                                onChange={(val) => {
                                  this.state.ownerDetails.carModel = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div> */}
                          {/* <div className="col-md-6">
                            <div className="field-holder">
                              <span className="fa fa-id-badge" />
                              <input
                                className="input_field"
                                type="number"
                                name="carNo"
                                placeholder="Car Number"
                                value={this.state.ownerDetails.carNo}
                                onChange={(val) => {
                                  this.state.ownerDetails.carNo = val.target.value
                                  this.setState({})
                                }}
                              />
                            </div>
                          </div> */}
                          <div className="col-md-6">
                            <a href="#" className="forget-pass">
                              Forget Password?
                            </a>
                            <label
                              htmlFor="termsOwner"
                              className="mt-3 text-dark font-weight-normal"
                            >
                              <input
                                type="checkbox"
                                name="termsOwner"
                                id="termsOwner"
                                className="mr-3"
                                checked = {this.state.ownerDetails.terms}
                                onClick = {() => {
                                  this.state.ownerDetails.terms = !this.state.ownerDetails.terms
                                  this.setState({})
                                }}
                              />
                              I accept all the stated terms &amp; conditions
                            </label>
                          </div>
                          <div className="col-md-6">
                            <button
                              type="button"
                              className="reg-btn"
                              onClick = {this.ownerRegister}
                            >
                              Signup{" "}
                              <i
                                className="fa fa-arrow-circle-right"
                                aria-hidden="true"
                              />
                            </button>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
