import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class header extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    return (
      <header className="tj-header">
        <div className="container">
          <div className="row">
            {/*Toprow Content Start*/}
            <div className="col-md-3 col-sm-4 col-xs-12">
              <div className="tj-logo">
                <h1>
                  <Link to="/home">OmCab Booking</Link>
                </h1>
              </div>
            </div>
            <div className="col-md-3 col-sm-4 col-xs-12">
              <div className="info_box">
                <i className="fa fa-home" />
                <div className="info_text">
                  <span className="info_title">Address</span>
                  <span>Cab Booking Address Here</span>
                </div>
              </div>
            </div>
            <div className="col-md-3 col-sm-4 col-xs-12">
              <div className="info_box">
                <i className="fa fa-envelope" />
                <div className="info_text">
                  <span className="info_title">Email Us</span>
                  <span>
                    <a href="mailto:demo@example.com">demo@example.com</a>
                  </span>
                </div>
              </div>
            </div>
            <div className="col-md-3 col-xs-12">
              <div className="phone_info">
                <div className="phone_icon">
                  <i className="fas fa-phone-volume" />
                </div>
                <div className="phone_text">
                  <span>
                    <a href="tel:1-234-000-2345">+1-234-567-890</a>
                  </span>
                </div>
              </div>
            </div>
            {/*Toprow Content End*/}
          </div>
        </div>
        <div className="tj-nav-row">
          <div className="container">
            <div className="row">
              {/*Nav Holder Start*/}
              <div className="d-flex tj-nav-holder w-100">
                <nav className="navbar navbar-default py-0">
                  <div className="navbar-header">
                    <button
                      type="button"
                      className="navbar-toggle collapsed"
                      data-toggle="collapse"
                      data-target="#tj-navbar-collapse"
                      aria-expanded="false"
                    >
                      <span className="sr-only">Menu</span>
                      <span className="icon-bar" />
                      <span className="icon-bar" />
                      <span className="icon-bar" />
                    </button>
                  </div>
                  {/* Navigation Content Start */}
                  <div
                    className="collapse navbar-collapse"
                    id="tj-navbar-collapse"
                  >
                    <ul className="nav navbar-nav flex-row">
                      <li className="dropdown">
                        <Link to="/home" className="">
                          Home
                        </Link>
                      </li>
                      
                      <li>
                        <Link to="/contact">Contact us</Link>
                      </li>
                    </ul>
                  </div>
                </nav>
                {/*Menu Holder End*/}
                <div className="book_btn">
                  <Link to="/register">
                    Register Now{" "}
                    <i
                      className="fa fa-arrow-circle-right"
                      aria-hidden="true"
                    />
                  </Link>
                </div>
              </div>
              {/*Nav Holder End*/}
            </div>
          </div>
        </div>
      </header>
    );
  }
}
